<template>
  <span class="inline">
    <span
      v-show="!field.editable"
      @dblclick="field.editable = true"
      v-text="field.text"
    />
    <input  
      ref="editableField"
      class="border-b border-green-300 w-full"
      v-show="field.editable"
      :value="field.text"
      @input="field.text = $event.target.value"
      v-on:blur="field.editable = false"
      @keyup.enter="field.editable = false"
    />
  </span>
</template>

<script>
export default {
  props: {
    field: {
      type: Object,
      required: true,
      default: () => { return ({ text: '', editable: false }) }
    }
  }
};
</script>
